import React, { useContext, useState } from 'react';
import { Card, CardHeader, CardBody, CardFooter, Button, Col, Container, Input, InputGroup, InputGroupAddon, InputGroupText, Row } from 'reactstrap';
import logo from '../../../assets/img/brand/logo.png';
import { UserContext } from '../../../context/contexts';
import firebase from '../../../firebase/Firebase';
import { Redirect } from 'react-router-dom';

function Verify() {

  const { email, displayName, emailVerified } = useContext(UserContext);
  const [verificationResend, setVerificationResend] = useState(false);

  const sendVerificationMail = () => {
    firebase.auth().currentUser.sendEmailVerification()
      .then(() => {
        setVerificationResend(true);
      })
      .catch((error) => {
        alert(error.message);
      })

  }

  return (
    <div className="app flex-row align-items-center">
      {emailVerified ? <Redirect to="/market" /> : null}
      <Container>
        <Row className="justify-content-center">
          <Col md="6">
            <Card>
              <CardHeader>
                <img src={logo} />
              </CardHeader>
              <CardBody>
                <span><strong>{displayName}</strong> Your Nepsechart account is ready. </span>
                <span>Please check your inbox <strong>{email}</strong> for verification email.</span>
                <div className="mt-3">If you didn't receive verification email.</div>
                {verificationResend ?
                  <div>Verfication mail sent. Please check your inbox</div>
                  :
                  <div> <Button onClick={sendVerificationMail} className="align-items-center bg-primary">Resend Verification mail</Button></div>
                }
              </CardBody>
              <CardFooter>
                <small>Also check <strong>spam</strong> folder if you cannot find verification email.</small>
              </CardFooter>
            </Card>
          </Col>
        </Row>
      </Container>
    </div>
  );
}

export default Verify;
